<template>
  <div id="outerWrapper">

    <!-- Navigation -->
    <nav class="navbar navbar-toggleable-md navbar-dark scrolling-navbar cyan darken-4">
      <div class="container">
        <a class="navbar-brand col-md-8" href="#">
          <h3 id="appTitle">Translation Helper 2.0.10</h3>
        </a>
        <div class="collapse navbar-collapse col-md-4 text-right" id="navbarNav">
          <ul class="navbar-nav mr-auto">
            <small class="text-muted">
              <a href="http://translate.yandex.com/">Powered by Yandex.Translate</a>
            </small>
          </ul>
        </div>
      </div>
    </nav>

    <!-- Main Content -->
    <div class="text-center container mt-1" id="app">
      <div class="row text-xs-left">
  		    <!--First column-->
  				<div class="col-lg-4 col-md-8 teal lighten-5 black-text p-m rounded-left animated fadeIn">
            <TranslateForm  v-on:propertyAdded="translateText"
                            v-on:resetData="resetData">
            </TranslateForm>
          </div>
          <!--Second column-->
  				<div class="col-lg-8 col-md-16 cyan darken-4 white-text text-left rounded-right animated fadeInDown">
              <TranslationResult v-bind:translatedText="htmlResponse"></TranslationResult>
          </div>
      </div>
      <div id="modalWindow" class="row">
        <Spinner/>
      </div>
    </div>

  </div> <!-- End outerWrapper -->
</template>

<script>
import Spinner from './components/Spinner';
import TranslateForm from './components/TranslateForm';
import TranslationResult from './components/TranslationResult';

// Instantiate the Translation Engine
let Translations = require('./Translations')();

$("#modalWindow").hide();

export default {
  name: 'app',
  components:{
    Spinner,
    TranslateForm,
    TranslationResult
  },
  data: function(){
    return {
      htmlResponse:'Add key name, text for translation and language codes'
    }
  },
  methods: {
    translateText:function(propertyName, text, languages){
      $("#modalWindow").show();
      Translations.translateText(propertyName, text, languages,
          // Arrow function as return handler to keep the context
          (data) => {
            this.htmlResponse = data;
            $("#modalWindow").hide();
          }
        )
    },
    resetData: function(){
      Translations.reset();
    }
  }
}
</script>

<style>
body {
  background:#fefefe;
}
#modalWindow{
  background:#333;  opacity: 0.5;
  position:absolute; top:0; vertical-align: center;
  width:100%; height: 100%;
  padding-top: 5em;
  display: none;
}
</style>



