<template>
  <div id="translateForm">
    <form id="transForm" class="form-inline p-2" v-on:submit="addEntry">
      <input class="p-1" type="text" v-model="languagesList" placeholder="Comma separated Language codes"/>
      <input class="p-1" type="text" v-model="propertyName" placeholder="Property Name"/>
      <input class="p-1" type="text" v-model="translateText" placeholder="Text for translation"/>
      <input class="btn cyan darken-4 mt-1" type="submit" value="Translate"/>
      <input class="btn teal darken-1 btn-sm mt-2" type="button" value="Reset" v-on:click="resetData" />
    </form>
  </div>
</template>

<script>
export default {
  name: 'translateForm',
  data(){
    return {
      propertyName:'',
      translateText:'',
      languagesList:''
    }
  },
  methods: {
    addEntry(e){
      this.$emit('propertyAdded', this.propertyName, this.translateText, this.languagesList);
      e.preventDefault();
    },
    resetData(e){
      this.$emit('resetData');
    }
  }
}
</script>



